/* PDFViewer.css - Styles for the PDF Viewer page */

/* Root container */
.pdf-viewer {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: var(--bg-primary);
    color: var(--text-primary);
}

.pdf-viewer.dark {
    background: var(--bg-dark);
    color: var(--text-primary);
}

.pdf-viewer-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

/* Toolbar */
.pdf-toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    gap: 0.5rem;
}

.toolbar-left,
.toolbar-center,
.toolbar-right {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.toolbar-btn {
    background: var(--bg-tertiary);
    border: none;
    border-radius: 8px;
    padding: 0.5rem 0.75rem;
    cursor: pointer;
    color: var(--text-primary);
    display: flex;
    align-items: center;
    gap: 0.25rem;
    transition: background 0.2s ease;
}

.toolbar-btn:hover {
    background: var(--bg-secondary);
}

.toolbar-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.page-info,
.zoom-info {
    font-size: 0.9rem;
}

.page-jump input {
    width: 4rem;
    padding: 0.25rem;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    text-align: center;
    background: var(--bg-tertiary);
    color: var(--text-primary);
}

/* PDF content */
.pdf-content {
    flex: 1;
    overflow: auto;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    background: var(--bg-secondary);
}

/* Loading spinner */
.pdf-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}

.spinner {
    border: 4px solid rgba(255, 255, 255, 0.2);
    border-top: 4px solid var(--primary-color);
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    animation: spin 1s linear infinite;
    margin-bottom: 0.5rem;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Summary removed for minimal stable UI */

/* Error and loading containers */
.loading-container,
.error-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
}

/* Book info */
.book-info h2 {
    margin: 0;
    font-size: 1.5rem;
}

.book-info p {
    margin: 0.25rem 0 0;
    font-size: 1rem;
    color: var(--text-secondary);
}
